import React from "react";
import "./CompetitiveSites.css";
import { OverlayTrigger, Tooltip } from "react-bootstrap";

// Competitive sites props
export interface CompetitiveSitesProps {
  logos: Array<{
    siteName: string;
    iconifyClassname: string;
    style: React.CSSProperties;
    profileLink: string;
  }>;
  theme?: any;
  className?: string;
}

const CompetitiveSites: React.FC<CompetitiveSitesProps> = ({ logos }) => {
  return (
    <div className="competitive-sites-main-div">
      <ul className="dev-icons">
        {logos.map((logo) => {
          return (
            <OverlayTrigger
              key={logo.siteName}
              placement={"top"}
              overlay={
                <Tooltip id={`tooltip-top`}>
                  <strong>{logo.siteName}</strong>
                </Tooltip>
              }
            >
              <li
                className="competitive-sites-inline"
                data-name={logo.siteName}
              >
                <a
                  href={logo.profileLink}
                  target="_blank"
                  rel="noopener noreferrer"
                >
                  <span
                    className="iconify"
                    data-icon={logo.iconifyClassname}
                    style={logo.style}
                    data-inline="false"
                  ></span>
                </a>
              </li>
            </OverlayTrigger>
          );
        })}
      </ul>
    </div>
  );
};

export default CompetitiveSites;
